<template>
<div id="home">
    <div v-if='isShow' class="item-1-circle" :class="isShow?'animated rotateIn':''">前端实习生 简历</div>
</div>
</template>

<script>
import {
    reactive,
    toRefs
} from 'vue'
export default {
    setup() {
        const state = reactive({
            isShow: false
        })

        return {
            ...toRefs(state),
        }
    },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            vm.isShow = true
        })
    }
};
</script>

<style lang="scss">
#home {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background: url("../assets/banner01.jpg") no-repeat center center;
    background-size: cover;

    .item-1-circle {
        // display: none;
        text-align: center;
        margin: 0 auto;
        margin-top: -100px;
        color: white;
        font-size: 50px;
        font-weight: 600;
        transition: all 2s;
    }

    .nextpage {
        position: absolute;
        left: 50%;
        margin-left: -40px;
        bottom: 100px;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 1px solid rgba(255, 255, 255, .3);
        cursor: pointer;
        overflow: hidden;

        img {
            position: absolute;
            width: 50%;
            top: -50%;
            left: 50%;
            margin-left: -25%;
        }
    }
}

@media screen and(max-width: 400px) {
    #home {
        .item-1-circle {
            font-size: 35px;
        }
    }
}
</style>
